import PropTypes from 'prop-types';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import AddAPhotoIcon from '@mui/icons-material/AddAPhoto';

export default function ImageSelector({ handleChange }) {
  return (
    <Box sx={{ display: 'flex', justifyContent: 'center', pb: 1 }}>
      <Button
        variant="contained"
        component="label"
        color="button"
        sx={{
          borderRadius: 5, border: 2, borderColor: 'primary.main', backgroundColor: 'input.background',
        }}
      >
        <input type="file" hidden multiple accept="image/*" onChange={handleChange} />
        <Typography variant="h6">Add Pictures</Typography>
        <AddAPhotoIcon sx={{ fontSize: '2.5rem' }} />
      </Button>
    </Box>
  );
}

ImageSelector.propTypes = {
  handleChange: PropTypes.func.isRequired,
};
