import PropTypes from 'prop-types';
import Box from '@mui/material/Box';

export default function SelectedImage({
  photo, index, setDimensions, thumbnail, resizeHeights,
}) {
  const handleImageOnload = (event, heights) => {
    const originalWidth = event.target.naturalWidth;
    const originalHeight = event.target.naturalHeight;
    const images = heights.map((height) => ({
      height, width: Math.ceil((height * originalWidth) / originalHeight),
    }));
    setDimensions(index, images);
  };

  return (
    <Box
      component="img"
      onLoad={(event) => handleImageOnload(event, resizeHeights)}
      sx={{
        border: (thumbnail === index) ? 4 : 0,
        borderColor: 'primary.main',
        borderRadius: (thumbnail === index) ? 5 : 0,
        mb: 1,
        '&:hover': {
          border: 5,
          borderColor: 'secondary.main',
        },
      }}
      src={photo}
      alt={photo.name}
      height="140px"
      width="140px"
      fit="crop"
      auto="format"
      loading="lazy"
    />
  );
}

SelectedImage.propTypes = {
  photo: PropTypes.string.isRequired,
  index: PropTypes.number.isRequired,
  setDimensions: PropTypes.func.isRequired,
  thumbnail: PropTypes.number.isRequired,
  resizeHeights: PropTypes.arrayOf(PropTypes.number).isRequired,
};
