import PropTypes from 'prop-types';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import RemovePhotoIcon from './RemovePhotoIcon';
import ThumbnailChooser from './ThumbnailChooser';
import SelectedImage from './SelectedImage';

export default function ImagePreviewer({
  imageDetails,
  handleRemoveImage,
  thumbnail,
  handleThumbnailChange,
  setDimensions,
  resizeHeights,
}) {
  return imageDetails.length
    ? (
      <>
        <Divider />
        <Typography sx={{ p: 2 }} variant="h6" align="center">
          Selected Images
        </Typography>
        <Grid
          container
          alignItems="center"
          justifyContent="space-around"
          sx={{ p: 1 }}
        >
          {imageDetails.map((photo, index) => (
            <Button key={photo.url} component="div">
              <SelectedImage
                photo={photo.url}
                index={index}
                setDimensions={setDimensions}
                thumbnail={thumbnail}
                resizeHeights={resizeHeights}
              />
              <RemovePhotoIcon handleClick={() => handleRemoveImage(index)} />
              <ThumbnailChooser
                index={index}
                thumbnail={thumbnail}
                handleThumbnailChange={handleThumbnailChange}
              />
            </Button>
          ))}
        </Grid>
      </>
    )
    : null;
}

ImagePreviewer.propTypes = {
  imageDetails: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    url: PropTypes.string,
    size: PropTypes.number,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
  handleRemoveImage: PropTypes.func.isRequired,
  thumbnail: PropTypes.number.isRequired,
  handleThumbnailChange: PropTypes.func.isRequired,
  setDimensions: PropTypes.func.isRequired,
  resizeHeights: PropTypes.arrayOf(PropTypes.number).isRequired,
};
