import { useState } from 'react';

export default function useSelectedImages() {
  const [imageDetails, setImageDetails] = useState([]);
  const [imageFiles, setImageFiles] = useState([]);
  const [thumbnail, setThumbnail] = useState(0);

  const handleThumbnailChange = (index) => {
    setThumbnail(index);
  };

  const handleSelectedImageChange = (event) => {
    if (event.target.files) {
      const files = Array.from(event.target.files);
      files.forEach((file) => {
        const image = new Image();
        image.onload = (e) => {
          const date = Date.now();
          const newName = `${date}-${file.name}`;
          setImageDetails((arr) => arr.concat([
            {
              name: newName,
              size: file.size / (1024 * 1024),
              url: e.target.src,
              height: e.target.naturalHeight,
              width: e.target.naturalWidth,
              imageFile: file,
            },
          ]));
        };
        image.src = URL.createObjectURL(file);
      });
      setImageFiles((arr) => arr.concat(files));
      files.map((file) => URL.revokeObjectURL(file)); // avoid memory leak
    }
  };

  const removeSelectedImage = (index) => {
    setImageDetails([
      ...imageDetails.slice(0, index),
      ...imageDetails.slice(index + 1),
    ]);

    setImageFiles([
      ...imageFiles.slice(0, index),
      ...imageFiles.slice(index + 1),
    ]);

    if (index < thumbnail) {
      handleThumbnailChange(thumbnail - 1);
    } else if (index === thumbnail) {
      handleThumbnailChange(0);
    }
  };

  const setDimensions = (index, images) => {
    setImageDetails([
      ...imageDetails.slice(0, index),
      {
        ...imageDetails[index],
        images,
      },
      ...imageDetails.slice(index + 1),
    ]);
  };

  return [
    imageDetails,
    thumbnail,
    handleSelectedImageChange,
    removeSelectedImage,
    handleThumbnailChange,
    setDimensions,
  ];
}
